    "title": "Annotator Bias",
    "podcast_title": "Data Skeptic",
    "publisher": "Kyle Polich",
    "description": "<p>The modern deep learning approaches to natural language processing are voracious in their demands for large corpora to train on.  Folk wisdom estimates used to be around 100k documents were required for effective training.  The availability of broadly trained, general-purpose models like BERT has made it possible to do transfer learning to achieve novel results on much smaller corpora.</p> <p>Thanks to these advancements, an NLP researcher might get value out of fewer examples since they can use the transfer learning to get a head start and focus on learning the nuances of the language specifically relevant to the task at hand.  Thus, small specialized corpora are both useful and practical to create.</p> <p>In this episode, Kyle speaks with <a href= \"https://mega002.github.io/\">Mor Geva</a>, lead author on the recent paper <a href=\"https://arxiv.org/abs/1908.07898\">Are We Modeling the Task or the Annotator? An Investigation of Annotator Bias in Natural Language Understanding Datasets</a>, which explores some unintended consequences of the typical procedure followed for generating corpora.</p> <p>Source code for the paper available here: <a href= \"https://github.com/mega002/annotator_bias\">https://github.com/mega002/annotator_bias</a></p> <p> </p>",
