    "description": "<p>In this episode, you’ll learn about language learning from AI (Artificial Intelligence) linguist and English teacher, Yazzy Ares.</p>\n<p>In this podcast we’ll talk about the field of NLP (Natural Language Processing) and AI, and the difference between humans and computers when it comes to language processing and language acquisition.<br />\nWe also talked about biases and discrimination when it comes to language learning and what we can learn about it from computers.</p>\n<p>And if you are an English student or teacher, you’ll also understand how to use this AI to improve your pronunciation!</p>\n<p>To watch the video interview with Yazzy and see her presentation go here: <a href=\"https://youtu.be/7FP5ikdqlCs\" target=\"_blank\" rel=\"noopener\">https://youtu.be/7FP5ikdqlCs</a></p>\n<hr />\n<p>Check out Yazzy’s website: <a href=\"https://www.yazzyares.com/\" target=\"_blank\" rel=\"noopener\">https://www.yazzyares.com/</a><br />\nFollow her on Instagram: <a href=\"https://www.instagram.com/yazzy.ares/\" target=\"_blank\" rel=\"noopener\">https://www.instagram.com/yazzy.ares/</a><br />\nIf you are doing linguistic research and need a hand with your data, you can contact her there.</p>",
