    "publisher": "Sandra Coral",
    "description": "<p>This episode talks a little about the story of someone I work with and from an intersectional lens (being an immigrant to a &quot;western country&quot;, second language speaker and a person of colour). The problems that he was going through in order to get a proper diagnosis and thoughts on three things have made it difficult for him to get the proper diagnosis and support that he needed. These are merely some initial thoughts as I go deeper into this in upcoming episodes specifically when looking at immigrants, refugees and asylum seekers and neurodivergence...<br/><br/><em>CORRECTION: I mention PDA profile autistics and making eye contact with ease, but actually want to clarify that it LOOKS as if it&apos;s easy and natural, but it&apos;s not always the case! It can be extremely spoon-draining to mask in ways where you&apos;re forcing yourself to make eye contact as a  PDA autistic too!</em></p><p>Three thoughts on: <br/>1. Lack of cultural competency<br/>2. Language barriers<br/>3. Clinician bias of language</p><p>Connect with me:<br/>IG: <a href='https://www.instagram.com/nd.narratives'>@nd.narratives</a><br/>Twitter: <a href='https://www.twitter.com/ndnarratives'>@ndnarratives</a><br/>Site: <a href='https://www.neurodivergentnarratives.com/'>Neurodivergent Narratives</a><br/><br/>Click for my free EBOOK on self-compassion: <a href='https://bit.ly/2Zv455h'><em>They Were Wrong About You</em></a><em>!</em></p><p><a rel=\"payment\" href=\"https://www.patreon.com/neurodivergentnarratives\">Support the show</a> (https://www.patreon.com/neurodivergentnarratives)</p>",
