    "title": "Ep 208: Explaining AI In Recruiting (Part 2)",
    "podcast_title": "Recruiting Future with Matt Alder",
    "publisher": "Evergreen Podcasts",
    "description": "<p>Following on from <a href=\"https://rfpodcast.com/2019/09/ep-207-explaining-ai-in-recruiting-part-1/\">my conversation with Megan Butler</a> about the state of the market for AI in recruitment, for the second part of this mini-series explaining AI and Machine learning, we are taking a deep dive into the technology and terminology itself.</p> <p>My guest is <a href=\"https://www.linkedin.com/in/jonkrohn/\">Jon Krohn</a>, Chief Data Scientist at recruiting technology company <a href=\"https://www.untapt.com/\">Untapt</a>. Jon is also a lecturer, researcher and author in the field of machine learning, deep learning and natural language processing. This interview goes into a huge amount of detail into the definitions and mechanics of AI (in an understandable way!) as well as exploring current and future applications for recruiting.</p> <p>In the interview, we discuss:</p> <p>• The role of a data scientist</p> <p>• AI and sorting the mail</p> <p>• Building predictive data models</p> <p>• Machine learning versus deep learning and how Siri got clever</p> <p>• Data engineering</p> <p>• Adoption and bias</p> <p>• Current and future applications of AI in recruiting</p> <p><br> <a href=\"https://itunes.apple.com/podcast/recruiting-future-podcast/id963756980?mt=2&amp;ls=1\">Subscribe to this podcast</a> in <a href=\"https://itunes.apple.com/podcast/recruiting-future-podcast/id963756980?mt=2&amp;ls=1\">Apple Podcasts</a></p>",
    "audio": "https://chtbl.com/track/87A8DC/traffic.megaphone.fm/FPMN6980039320.mp3",
