    "title": "48: Thinking Machines (Philosophical Basis of Artificial Intelligence)",
    "podcast_title": "Breaking Math Podcast",
    "publisher": "Breaking Math Podcast",
    "description": "<p>Machines, during the lifetime of anyone who is listening to this, have advanced and revolutionized the way that we live our lives. Many listening to this, for example, have lived through the rise of smart phones, 3d printing, massive advancements in lithium ion batteries, the Internet, robotics, and some have even lived through the introduction of cable TV, color television, and computers as an appliance. All advances in machinery, however, since the beginning of time have one thing in common: they make what we want to do easier. One of the great tragedies of being imperfect entities, however, is that we make mistakes. Sometimes those mistakes can lead to war, famine, blood feuds, miscalculation, the punishment of the innocent, and other terrible things. It has, thus, been the goal of many, for a very long time, to come up with a system for not making these mistakes in the first place: a thinking machine, which would help eliminate bias in situations. Such a fantastic machine is looking like it's becoming closer and closer to reality, especially with the advancements in artificial intelligence. But what are the origins of this fantasy? What attempts have people made over time to encapsulate reason? And what is ultimately possible with the automated manipulation of meaning? All of this and more on this episode of Breaking Math. Episode 48: Thinking Machines References: * https://publicdomainreview.org/essay/let-us-calculate-leibniz-llull-and-the-computational-imagination * https://spectrum.ieee.org/tag/history+of+natural+language+processing https://en.wikipedia.org/wiki/Characteristica_universalis https://ourworldindata.org/coronavirus-source-data This episode is distributed under a CC BY-SA 4.0 license. For more information, visit CreativeCommons.org. [Featuring: Sofía Baca, Gabriel Hesch]<br>\n<br>\n<u><strong>Ways to support the show:</strong></u></p>\n<p>-<strong>Visit our Sponsors: &nbsp; &nbsp;&nbsp;&nbsp;&nbsp;</strong><a href=\"https://www.thegreatcoursesplus.com/BreakingMath\">theGreatCoursesPlus.com/breakingmath&nbsp;</a>Get a <strong>free month</strong> of the <strong>Great Courses Plus</strong> while supporting this show by clicking the link and signing up! &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href=\"https://brilliant.org/breakingmath\">brilliant.org/breakingmath</a> Sign up at brilliant.org, where breaking math listeners get a 20% off of a year's subscription of Brilliant Premium!</p>\n<p><u><strong>Patreon</strong></u><strong> </strong>Become a monthly supporter at <a href=\"https://patreon.com/breakingmath\">patreon.com/breakingmath</a></p>\n<p><u><strong>Merchandise</strong></u><strong> </strong>Purchase a Math Poster on Tensor Calculus at our facebook store at <a href=\"https://facebook.com/breakingmathpodcast\">facebook.com/breakingmathpodcast</a></p>\n\n--- \n\nThis episode is sponsored by \n· Anchor: The easiest way to make a podcast.  <a href=\"https://anchor.fm/app\">https://anchor.fm/app</a>\n\nSupport this podcast: <a href=\"https://anchor.fm/breakingmathpodcast/support\" rel=\"payment\">https://anchor.fm/breakingmathpodcast/support</a>",
