    "title": "Natural Language Understanding - Walid Saba",
    "podcast_title": "Minhaaj's Podcast",
    "publisher": "minhaaj rehman",
    "description": "<p>Walid S. Saba is the Founder and Principal AI Scientist at ONTOLOGIK.AI where he works on the development of Conversational AI. Prior to this, he was a PrincipalAI Scientist at Astound.ai and Co-Founder and the CTO of Klangoo. He also held various positions at such places as the American Institutes for Research, AT&amp;TBell Labs, Metlife, IBM and Cognos, and has spent 7 years in academia where he taught computer science at the New Jersey Institute of Technology, theUniversity of Windsor, and the American University of Beirut (AUB). Dr. Saba is frequently an invited speaker at various organizations and is also frequently invited to various panels and podcasts that discuss issues related to AI and Natural Language Processing. He has published over 40 technical articles, including an award-winning paper that was presented at theGerman Artificial Intelligence Conference in 2008. Walid holds a BSc and an MSc in Computer Science as well as a Ph.D. in Computer Science (AI/NLP) which he obtained from Carleton University in 1999.</p>\n<p><br></p>\n<p>00:00 intro</p>\n<p>01:00 Language as a mental construct, PAC, &nbsp;Subtext in Sentences</p>\n<p>06:28 OpenAI’s Codex Platform, Below Human Baseline Performance of NLP</p>\n<p>18:00 Comprehension vs Generation, &nbsp;Search vs Context</p>\n<p>19:20 Sophia the Robot, Shallow ethics in AI and Commercialisation of Academia</p>\n<p>27:40 Bad Research Papers, Facebook runaway train &amp; AI Godfathers Cult.</p>\n<p>32:30 AI leaders and Profiteering, Unethical Behaviour of Influencers.</p>\n<p>37:50 Non-Verbal Component of Natural Language Understanding, Prosody and Accuracy Boost</p>\n<p>41:33 Ontologik’s NLU Engine, Adjective Ordering Restriction Mystery&nbsp;</p>\n<p>43:58 Ontological Structure and Chomsky’s Universal Grammar, Discovery vs Creation</p>\n<p>45:31 Entity Extraction and How Ontologik’s Engine tackles this Problem</p>\n<p>47:50 Language Agnostic Learning, Foreign Language Learning, and Pedagogy of Linguistics</p>\n<p>54:00 First Language, Blank State and Missing Sounds in Some Languages</p>\n<p>55:20 Real-time Language Translation Engines, AR/VR Aids and Commercial Utility</p>\n<p>01:01:00 Sentiment Analysis, Language Policing &amp; Censorship&nbsp;</p>\n<p>01:04:00 Ontological Structures, Gender Bias and Situational Paradox</p>\n<p>01:09:00 3 Foods for Rest of the Life &amp; Fad Food Indulgence\t</p>\n<p>01:11:00 Inspiration for Getting into the Field, Career Ideals &amp; Cultural Influence</p>\n<p>01:15:30 Epistemology, IQ and The Bell Curve&nbsp;</p>\n<p>01:17:00 Einstein’s IQ, Haircut, Social Skills, and Success Rubric</p>\n<p>01:22:00 Attracting Brilliant Talent Around the World, Ivy League PhDs &amp; Standardised Testing</p>\n<p>01:28:40 Unsupervised Learning, Accuracy &amp; Comprehensibility in NLU</p>\n<p>01:30:20 BF Skinner, Pavlovian Dogs, Skinner has been Skinned.</p>\n<p>01:37:50 Human Behavioral Biology, Endocrinal System similarities with Humans yet they don’t learn Languages.</p>\n<p>01:45:30 Language as an expression of Genetic differences, Big Five &amp; Phenotype.</p>\n<p>01:49:40 IBM Watson Personality Insights, Text-based personality Inferences.</p>\n<p>01:55:30 Long Short Term Memory Issue in Ontologik’s Engine, Computational Complexity, Timeline for Release</p>\n<p><br></p>",
