    "title": "ML with Dale Markowitz",
    "podcast_title": "Google Cloud Platform Podcast",
    "publisher": "Google Cloud Platform",
    "description": "<p>On the podcast this week, we have a great interview with Google Developer Advocate, <a href=\"https://twitter.com/dalequark\">Dale Markowitz</a>. <a href=\"https://twitter.com/the_thagomizer\">Aja Hammerly</a> and <a href=\"https://twitter.com/syntxerror1\">Jon Foust</a> are your hosts, as we talk about machine learning, its best use cases, and how developers can break into machine learning and data science. Dale talks about natural language processing as well, explaining that it&rsquo;s basically the intersection of machine learning and text processing. It can be used for anything from aggregating and sorting Twitter posts about your company to sentiment analysis.</p>\n\n<p>For developers looking to enter the machine learning space, Dale suggests starting with non life-threatening applications, such as labeling pictures. Next, consider the possible mistakes the application can make ahead of time to help mitigate issues. To help prevent the introduction of bias into the model, Dale suggests introducing it to as many different types of project-appropriate data sets as possible. It&rsquo;s also important to continually monitor your model.</p>\n\n<p>Later in the show, we talk Google shop, learning about all the new features in Google Translate and AutoML.</p>\n\n<p></p>",
