    "title": "2018 End of Year Special (Part 2):  Predictions for 2019",
    "podcast_title": "Giving Thought",
    "publisher": "Giving Thought",
    "description": "<p>In part 2 of our bumper end of year special (episode 40), we make some predictions for what 2019 might hold...</p> <p> </p> <p>2019 Predictions:</p> <p><em>Politics & Economics</em></p> <ul> <li>BREXIT (or not…?)</li> <li>General Election?</li> <li>UK voluntary reporting on SDGs</li> <li>Ongoing impact of austerity</li> <li>Local government funding and Local Authorities running out of money</li> <li>Populism & ongoing challenges for democracy</li> <li>Another global financial crash?</li> </ul> <p><em> </em></p> <p><em>Philanthropy & Civil Society</em></p> <ul> <li>More critiques of big philanthropy; general default more critical (also in UK?)</li> <li>Focus on think tanks, dark money etc</li> <li>Austerity: debating role of charity vs state</li> <li>Further closing space issues, including digital angles (misinformation, astroturfing etc)</li> <li>Networked models for social change</li> <li>Line between civil society and politics will come under increasing strain</li> <li>Shifting power; democratising philanthropy</li> <li>Next big fundraising/charity scandal?</li> </ul> <p><em> </em></p> <p><em>Technology</em></p> <ul> <li>Continuing tech backlash?</li> <li>More funders focussing on algorithmic bias</li> <li>Pushback on AI ethics- criticism that it frames the debate in the tech industry’s terms and allows them to avoid discussion of regulation etc.</li> <li>Divergence of cryptophilanthropy and blockchain efforts</li> <li>Blockchain will continue to bubble under surface and back office blockchain use case will be demonstrated</li> <li>Self Sovereign Digital Identity will come to the fore</li> <li>Charity RegTech applications will emerge</li> <li>Machine Learning and Natural Language Processing for data collection and analysis on charity finances, philanthropy data</li> <li>More giving by conversational AI</li> <li>More commercial platforms adding giving functionality</li> <li>eSports growth, digital “skins” and giving via gaming platforms (e.g. Twitch)</li> <li>Deepfakes and challenges to online authenticity</li> <li>More awareness of personal data bargain</li> <li>Charity Data fines</li> </ul>",
    "audio": "https://chtbl.com/track/38G77C/traffic.libsyn.com/secure/givingthought/EP_40_PART_2_-_2019_Predictions_MP3.mp3?dest-id=519706",
    "pub_date": "2018-12-20T06:00:13.061000-08:00",
